import style from './index.module.scss'
import { Table } from 'antd'

const column = [
    {
        key: 'ckey',
        dataIndex: 'ckey',
        render: (text: string) => (
            <h6 style={{ fontWeight: 'bold' }}>{text}</h6>
        ),
    },
    {
        key: 'value',
        dataIndex: 'value',
    },
]

const dataSource = [
    {
        key: '1',
        ckey: 'Tn thit b',
        value: 'My xc lt abc',
    },
    {
        key: '2',
        ckey: 'Loi thit b',
        value: 'MY XC O, XC LT',
    },
    {
        key: '3',
        ckey: 'Li my chnh',
        value: 'Nguyn Vn A',
    },
    {
        key: '4',
        ckey: 'Li my ph',
        value: 'Nguyn Vn B',
    },
    {
        key: '5',
        ckey: 'Qun l my',
        value: 'Trn Vn A',
    },
]

const DeviceProjectModal = () => {
    return (
        <div className={style.project_summary_container}>
            <Table
                className={style.project_table_content}
                columns={column}
                dataSource={dataSource}
                showHeader={false}
                pagination={false}
            />
        </div>
    )
}

export default DeviceProjectModal
